.formControl textarea {
    border-radius: 20px;
    padding-left: 10px;
    outline: none;
}

.formControl textarea::placeholder {
    color: black;
    text-align: center;
}

.formControl.error input,
.formControl.error textarea {
    border: solid red 1px;
}

.formControl.error span {
    color: red;
}

.formSummaryError {
    width: 150px;
    text-align: center;
    color: red;
    border: solid red 3px;
}
