.PostsBlock {
    padding: 10px 20px;
}

.container {
    flex-grow: 1;
    position: relative;
    height: 300px;
    max-height: 400px;
}

.posts {
    margin-top: 10px ;


    border: 2px solid rgb(0,20,41);

    max-height: 300px;
    padding: 20px 20px 30px;
    position: absolute;
    width: 100%;
    left: 0;
    bottom: 0;
    overflow: auto;
}

.posts::-webkit-scrollbar {
    width: 8px;
}

.posts::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
}

.posts::-webkit-scrollbar-thumb {
    /*-webkit-border-radius: 10px;*/
    /*border-radius: 10px;*/
    background: rgba(79, 79, 79, 0.8);
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.5);
}

.posts::-webkit-scrollbar-thumb:window-inactive {
    background: rgba(78, 72, 72, 0.4);
}

